@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    display: grid;
    padding: 25px;
    padding-top: 5%;
    grid-template: 60px 1fr /  .75fr 1fr;
    grid-gap: 10px;
    height: 100vh;
    grid-template-areas: "Search Search"
                         "Characters Details"
}

@media screen and (max-width: 620px) {
    .App {
        grid-template: 60px 1fr 1fr / 1fr;
        grid-template-areas: "Search"
                             "Details"
                             "Characters" 
    }
}
body{
  background: #f2f2f2;
  font-family: 'Open Sans', sans-serif;
}

.SearchBar {
  width: 30%;
  position: relative;
  display: flex;  
  margin: 0 auto;
  grid-area: Search;
}

.SearchBar-input {
  width: 100%;
  border: 3px solid #00B4CC;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9DBFAF;
}

.SearchBar-input:focus{
  color: black;
}

.SearchBar-button {
  width: 40px;
  height: 36px;
  border: 1px solid #00B4CC;
  background: #00B4CC;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.SearchBar-icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor;
}

@media screen and (max-width: 932px) {
    .SearchBar {
        width: 70%;
    }
}
.Characters {
    grid-area: Characters;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.Character {
    border: gray 1px solid;
    margin: 5px;
    width: 70%;
    box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
    display: flex;
    padding: 1em;
    cursor: pointer;
}

.Character-left {
    margin-right: 1.5em;
}

.Character-rigth h3 {
    margin: 0;
}

@media screen and (max-width: 932px) {
    .Character {
        display: inline;
        display: initial;
        text-align: center
    }

    .Character-left {
        margin-right: 0;
    }
}
.Details {
    grid-area: Details;
    display: grid;
    grid-template: 75px 300px 1fr / 300px 1fr;
    grid-gap: 10px;
    grid-template-areas: "name name"
                         "image description"
                         "comics comics";
    padding: 10px;
    border: gray 1px solid;
    box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
}

.Details-title {
    grid-area: name;
    display: flex;
    justify-content: center;
    font-size: 2em;
    margin: 0;
}

.Details-image {
    grid-area: image;
    box-shadow: 1px 2px 5px 0px rgba(0,0,0,0.75);
    border-radius: 5px;
}

.Details-description {
    grid-area: description;
}

.Details-comics {
    grid-area: comics;
}

.Details-comics h4 {
    font-size: 1.5em;
    margin: 0;
}

.Details-comics li {
    list-style: none;
    margin-left: 1em;
}

@media screen and (max-width: 932px) {
    .Details {
        grid-area: Details;
        display: grid;
        grid-template: 75px 300px 200px 1fr / 1fr;
        grid-template-areas: "name"
                             "image"
                             "description"
                             "comics";
    }

    .Details-image {
        width: 100%;
    }
}
.Loading {
    display: flex;
    height: 100vh;
    width: 100vw;
    justify-content: center;
    align-items: center;
}
.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}
.lds-ellipsis div {
    position: absolute;
    top: 27px;
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background: #00B4CC;
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
            animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
    left: 6px;
    -webkit-animation: lds-ellipsis1 0.6s infinite;
            animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
    left: 6px;
    -webkit-animation: lds-ellipsis2 0.6s infinite;
            animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
    left: 26px;
    -webkit-animation: lds-ellipsis2 0.6s infinite;
            animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
    left: 45px;
    -webkit-animation: lds-ellipsis3 0.6s infinite;
            animation: lds-ellipsis3 0.6s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
}
@keyframes lds-ellipsis1 {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
}
@-webkit-keyframes lds-ellipsis3 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
}
@keyframes lds-ellipsis3 {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
}
@-webkit-keyframes lds-ellipsis2 {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
    100% {
      -webkit-transform: translate(19px, 0);
              transform: translate(19px, 0);
    }
}
@keyframes lds-ellipsis2 {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
    }
    100% {
      -webkit-transform: translate(19px, 0);
              transform: translate(19px, 0);
    }
}
  
